<template>
	<div class="cart-number">
		<el-input-number :min="1" :max="100" size="mini" v-model="count" @change="countChange"></el-input-number>
	</div>
</template>

<script>
import _ from 'lodash';

export default {
	name: 'cart-number',
	props: ['goods'],
	data() {
		return {
			count: this.goods.count,
		};
	},
	methods: {
		// 增加或者减少个数
		countChange(count) {
			this.updateCartDebounce(this, count);
		},
		// 更新购物车防抖控制
		updateCartDebounce: _.debounce(async (vm, count) => {
			await vm.$store.dispatch('updateCart', { goods: vm.goods, count });
			vm.$emit('countUpdate');
		}, 1000),
	},
};
</script>

