<template>
	<div class="cart-view">
		<div v-if="!cartList.length" class="cart-empty-box">
			<i style="font-size: 60px" class="el-icon-shopping-cart-2"></i>
			<div>
				<p>购物车空空的哦~，去看看心仪的商品吧~</p>
				<router-link style="color: red" to="/">去购物 <i class="el-icon-arrow-right"></i></router-link>
			</div>
		</div>

		<div v-else>
			<el-table class="table" ref="multipleTable" :data="cartList" style="width: 100%" @selection-change="selectionChange">
				<el-table-column type="selection" width="100"></el-table-column>
				<el-table-column label="商品" width="400">
					<template slot-scope="scope">
						<router-link class="goods" :to="'/goodsdetail/' + scope.row.Id">
							<el-image fit="fill" class="img" :src="scope.row.imageUrl"></el-image>
							<div class="info">
								<p>
									<strong class="ellipsis2">{{ scope.row.title }} {{ scope.row.salePoint }}</strong>
								</p>
								<p v-html="scope.row.mack"></p>
							</div>
						</router-link>
					</template>
				</el-table-column>
				<el-table-column label="单价" width="100"
					><template slot-scope="scope">{{ scope.row.priceStr | price }}</template></el-table-column
				>
				<el-table-column label="数量" width="160">
					<template slot-scope="scope">
						<cart-number :goods="scope.row" @countUpdate="resetTotalPrice(selectionList)" :key="scope.row.Id"></cart-number>
					</template>
				</el-table-column>
				<el-table-column label="小结" width="100"
					><template slot-scope="scope">{{ (scope.row.priceStr * scope.row.count) | price }}</template></el-table-column
				>
				<el-table-column label="操作" width="100">
					<template slot-scope="scope">
						<el-button type="danger" plain icon="el-icon-delete" @click="dialogShowClick(scope.row)">删除</el-button>
					</template>
				</el-table-column>
			</el-table>
			<div class="total-box">
				<el-checkbox :indeterminate="isIndeterminate" v-model="isSelectedAll" @change="selectAll">全选</el-checkbox>
				<div class="settle-accounts">
					<span class="price"
						>总价: <strong>{{ totalPrice | price }}</strong></span
					>
					<el-button class="settle-btn" type="danger">结算</el-button>
				</div>
			</div>
		</div>

		<el-dialog title="提示" :visible.sync="dialogVisible" width="20%">
			<span>确定要删除这个商品吗?</span>
			<span slot="footer" class="dialog-footer">
				<el-button @click="cancelBtnClick">取 消</el-button>
				<el-button type="primary" @click="delBtnClick">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
import CartNumber from '@/components/CartNumber';

export default {
	name: 'cart-view',
	components: { CartNumber },
	data() {
		return {
			dialogVisible: false, //控制对话框
			goods: {}, //被删除的商品
			totalPrice: 0, //商品总价
			isSelectedAll: false, //是否全选
			isIndeterminate: false, //indeterminate 属性用以表示 checkbox 的不确定状态，一般用于实现全选的效果
			selectionList: [], //记录选中的商品
		};
	},
	computed: {
		cartList() {
			return this.$store.getters.cartList;
		},
	},
  activated() {
    
  },
	deactivated() {
		this.isSelectedAll = false;
	},
	methods: {
    async getCartList(){
      await this.$store.dispatch('getCartList');
    },
		// 计算总价
		resetTotalPrice(list) {
			this.totalPrice = list.reduce((prev, current) => {
				return prev + current.count * current.priceStr;
			}, 0);
		},
		// 全选和全不选按钮
		selectAll() {
			if (this.isSelectedAll) {
				this.$refs.multipleTable.toggleAllSelection(); //表格中选中所有
			} else {
				this.$refs.multipleTable.clearSelection(); //表格中清空所有选中
			}
		},
		// 当选择项发生变化时会触发该事件
		selectionChange(rows) {
			this.selectionList = rows;
			this.isSelectedAll = rows.length == this.cartList.length; //是否全选
			this.isIndeterminate = rows.length > 0 && rows.length < this.cartList.length; //控制半选状态
			this.resetTotalPrice(rows); //计算总价
		},
		// 显示对话框
		dialogShowClick(goods) {
			this.dialogVisible = true;
			this.goods = goods;
		},
		// 取消
		cancelBtnClick() {
			this.dialogVisible = false;
			this.goods = {};
		},
		// 删除商品
		async delBtnClick() {
			this.dialogVisible = false;
			await this.$store.dispatch('delCart', this.goods);
		},
		countChange() {
			this.resetTotalPrice(this.selectionList); //更新总价
		},
	},
};
</script>

<style scoped lang="scss">
.cart-empty-box {
	height: 400px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.cart-view .goods {
	display: flex;
	align-items: flex-start;
	padding: 2px 0;
	.img {
		flex-shrink: 0;
		width: 80px;
		height: 80px;
		box-shadow: 0 0 1px #333;
		margin-right: 5px;
	}
}

.total-box {
	margin: 10px 0;
	padding-left: 15px;
	box-shadow: 0 0 1px #333;
	line-height: 50px;
	display: flex;
	justify-content: space-between;
	.settle-accounts .price {
		display: inline-block;
		width: 230px;
		strong {
			color: #f56c6c;
			font-size: 20px;
		}
    .settle-btn {
		width: 100px;
		height: 100%;
	}
	}
}
</style>
